import React from "react";
import f from './users.module.css';
import smallIcon from '../../../src/Redux/Pictures/UserSmallIcon.png';
import { NavLink } from "react-router-dom";
import { useState } from "react";
import { memo } from "react";

const Users = (props) => {
    let pagesCount = Math.ceil(props.usersPage.totalCountOfUsers/5)
    let portionsCount = Math.ceil(pagesCount/5)
    let [currentPortion, changeCP] = useState(Math.ceil(props.currentPage/5))
    let leftNumber = (currentPortion*5)-4;
    let rightNumber = currentPortion*5
    let allPages = [];
    for (let i = 1; i <= pagesCount; i++) {
        allPages.push(i)
    }
    return <div>
        {(leftNumber >= 6) ?
            <button onClick={() => {
                changeCP(currentPortion-1)
            }}>LEFT</button>
            : undefined
        }
        <button onClick={() => {
                changeCP(portionsCount)
            }}>Last page</button>
        <div className={f.pagesBar}>
            {allPages.filter(p => p >= leftNumber && p <= rightNumber).map(u => <span className={(u === props.usersPage.currentPage) ? f.currentPage : f.page} onClick={() => {
                props.changeCurrentPage(u);
                props.getNewUsers(u)
            }}>{u}</span>)}
        </div>
        {(rightNumber !== true && portionsCount !== rightNumber)?
        <button onClick={() => {
            changeCP(currentPortion+1)
        }}>RIGHT</button>
        : undefined
    }
        
        <div>{
            props.usersPage.users.map(u => <div className={f.user} key={u.id}>
                <h3>{u.name}</h3>
                <NavLink className={f.NavLinkUser} to={`/profile/${u.id}`}><img className={f.userPhoto} src={(u.photos.small === null) ? smallIcon : u.photos.small}></img></NavLink>
                <div>
                    <p>User status: {u.status}</p>
                    <p>City: {u.city}</p>
                </div>
                <div>{u.followed ?
                    <button
                        disabled={props.usersPage.followingArray.some(id => id === u.id)}
                        className={f.unfollowButton}
                        onClick={() => {
                            props.unfollowThunkAC(u.id)
                        }}>Unfollow
                    </button> :
                    <button
                        disabled={props.usersPage.followingArray.some(id => id === u.id)}
                        className={f.followButton}
                        onClick={() => {
                            props.followThunkAC(u.id)
                        }}>Follow
                    </button>}
                </div>
            </div>
            )
        }
        </div>
    </div>
}

export default Users;