import axios from 'axios';

const instanse = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: { 'API-KEY': 'f9f2ae07-5780-4dee-abb2-728cdace788e' }
})

export const UsersAPI = {
    get(currentPage) {
        return (
        instanse.get(`users?page=${currentPage}&count=5`)
        )},
    delete(id) {
        debugger
        return (
            instanse.delete(`follow/${id}`)
        )},
    post(id) {
        return (
            instanse.post(`follow/${id}`)
        )
    }
}

export const AuthAPI = {
    authMe() {
        return (instanse.get('auth/me'))
    },
    authLoginPost(email, password, rememberMe) {
        return (instanse.post('auth/login', {
            email: email,
            password: password,
            rememberMe: rememberMe
        }))
    },
    authLoginDelete() {
        return (instanse.delete('auth/login')
        )
    }
}

export const ProfileAPI = {
    get(id) {
        return (
        instanse.get(`profile/${id}`)
        )
    },
    putStatus(text) {
        return (
            instanse.put('profile/status', {status: text})
        )
    },
    getStatus(id) {
        return (
            instanse.get(`profile/status/${id}`)
        )
    }

}
