import { UsersAPI } from "../API/Api";
import { mapHelper } from "./helpers";

let FOLLOW_UNFOLLOW = 'FOLLOW-UNFOLLOW';
let SET_USERS = 'SET-USERS';
let CHANGE_CURRENT_PAGE = 'CHANGE_CURRENT_PAGE';
let CHANGE_USERS_LOADING = 'CHANGE-USERS-LOADING';
let TOGGLE_FOLLOWING = 'TOGGLE-FOLLOWING';

let initialState = {
    users: [],
    currentPage: 1,
    totalCountOfUsers: null,
    pageSize: 5,
    isLoading: false,
    followingArray: []
}

const usersPageReducer = (state = initialState, action) => {
    switch (action.type) {
        case FOLLOW_UNFOLLOW: {
            return {
                ...state,
                users: mapHelper(state.users, 'id', action.userID, { followed: action.follow })
            }
        }
        case SET_USERS: {
            return {
                ...state,
                users: [...action.data.items],
                totalCountOfUsers: action.totalCountOfUsers
            }
        }
        case CHANGE_CURRENT_PAGE: {
            return {
                ...state,
                currentPage: action.currentPage
            }
        }
        case CHANGE_USERS_LOADING: {
            return {
                ...state,
                isLoading: action.loading
            }
        }
        case TOGGLE_FOLLOWING: {
            return {
                ...state,
                followingArray: (action.isFetching) ? [...state.followingArray, action.id] : state.followingArray.filter(id => id !== action.id)
            }
        }
        default: {
            return state;
        }
    }
}

export default usersPageReducer;

const AC = {
    toggleFollowing(isFetching, id) {
        return {
            type: TOGGLE_FOLLOWING,
            isFetching: isFetching,
            id: id
        }
    },
    followUnfollow(userID, follow) {
        return {
            type: FOLLOW_UNFOLLOW,
            userID: userID,
            follow: follow
        }
    },
    setUsers(data, totalCountOfUsers) {
        return {
            type: SET_USERS,
            data: data,
            totalCountOfUsers: totalCountOfUsers
        }
    },
    changeCurrentPage(currentPage) {
        return {
            type: CHANGE_CURRENT_PAGE,
            currentPage: currentPage
        }
    },
    changeUsersLoading(loading) {
        return {
            type: CHANGE_USERS_LOADING,
            loading: loading
        }
    },
    changeCurrentPage(currentPage) {
        return {
            type: CHANGE_CURRENT_PAGE,
            currentPage: currentPage
        }
    }
}

export const changeCurrentPage = (currentPage) => {
    return {
        type: CHANGE_CURRENT_PAGE,
        currentPage: currentPage
    }
}

const responseAwait = async (act, id, dispatch, follow) => {
    dispatch(AC.toggleFollowing(true, id))
    let response = await UsersAPI[act](id)
    if (response.data.resultCode === 0) {
        dispatch(AC.followUnfollow(id, follow))
        dispatch(AC.toggleFollowing(false, id))
    }
}

export const unfollowThunkAC = (id) => {
    return (dispatch) => {
        responseAwait('delete', id, dispatch, false)
    }
}

export const followThunkAC = (id) => {
    return (dispatch) => {
        responseAwait('post', id, dispatch, true)
    }
}

export const GetUsersThunkAC = (currentPage = 1) => {
    return (dispatch) => {
        dispatch(AC.changeUsersLoading(true))
        UsersAPI.get(currentPage).then(response => {
            dispatch(AC.setUsers(response.data, response.data.totalCount))
            dispatch(AC.changeUsersLoading(false))
        })
    }
}