import React from "react";
import { connect } from "react-redux";
import { addSupportMessageActionCreator, updateNewSupportMessageFieldActionCreator } from "../../Redux/supportPageReducer";
import Support from "./support";

let mapStateToProps = (state) => {
    return {
        state: state.supportPage
    }
}

let mapDispatchToProps = (dispatch) => {
    return {
        updateNewSupportMessageField: (text) => {dispatch(updateNewSupportMessageFieldActionCreator(text))},
        addSupportMessage: () => {dispatch(addSupportMessageActionCreator())}
    }
}

const SupportContainer = connect(mapStateToProps, mapDispatchToProps) (Support);

export default SupportContainer;