import { changeCurrentPage, GetUsersThunkAC, unfollowThunkAC, followThunkAC } from '../../Redux/usersPageReducer';
import Users from './users';
import {connect} from 'react-redux';
import React from 'react';
import Preloader from './preloader';
import { withAuthRedirect } from '../../hoc/hoc';
import { compose } from 'redux';
import { getUsersPage, UsersSelectors } from '../../Redux/Selectors';

class UsersAPIComponent extends React.Component {
    componentDidMount() {
        this.props.GetUsersThunkAC(this.props.currentPage)
    }
    getNewUsers = (currentPage) => {
        this.props.GetUsersThunkAC(currentPage)
    }
    render() {
        return (<div>
        {(this.props.usersPage.isLoading === true)? <Preloader /> : <Users
            getNewUsers={this.getNewUsers}
            usersPage={this.props.usersPage}
            changeCurrentPage={this.props.changeCurrentPage}
            follow={this.props.follow}
            unfollow={this.props.unfollow}
            toggleFollowing={this.props.toggleFollowing}
            unfollowThunkAC={this.props.unfollowThunkAC}
            followThunkAC={this.props.followThunkAC}
            currentPage={this.props.currentPage}
            />}
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        usersPage: UsersSelectors.getUsersPage(state),
        currentPage: state.usersPage.currentPage
    }
}

const UsersContainer = compose(
    withAuthRedirect,
    connect(mapStateToProps, { changeCurrentPage, GetUsersThunkAC, unfollowThunkAC, followThunkAC })
)(UsersAPIComponent);

export default UsersContainer;