import React from "react";
import p from './Validators.module.css';

export const Errors = {
    fieldRequired(value) {
        if (value) {
            return undefined
        } else {
            return 'Field is required'
        }
    },
    MaxLength(max) {
        return (value) => {
            if (value) {
                if (value.length > max) {
                    return `Max length is ${max} symbols`
                } else {
                    return undefined
                }
            }
        }
    }
}

export const Fields = {
    Input({ meta, input, ...props }) {
        return <div>
            <input {...input} {...meta} {...props}
                className={(meta.error && meta.touched) ? p.error : undefined} />
            <DisplayError touched={meta.touched} error={meta.error} />
        </div>
    },
    Textarea({ meta, input, ...props }) {
        return <div>
            <DisplayError touched={meta.touched} error={meta.error} />
            <textarea {...input} {...meta} {...props}
                className={(meta.error && meta.touched) ? p.error : undefined} />
        </div>
    }
}

const DisplayError = (props) => {
    return (
        <span className={p.errorName}>{(props.touched && props.error) ? props.error : undefined}</span>
    )
}
