const ADD_SUPPORT_MESSAGE = 'ADD-SUPPORT-MESSAGE';
const UPDATE_NEW_SUPPORT_TEXTFIELD = 'UPDATE-NEW-SUPPORT-TEXTFIELD';

let initialState = {
        newTextField: '',
        messages: [
            {user: '1', text: 'Hello. I have triuble with adding friends'},
            {user: '2', text: 'What sort of problem are we dealing with?'},
            {user: '1', text: 'I just cant add new friends, maybe thats because nobody wants to add me(((('}
        ]
}

const supportPageReducer = (state = initialState, action) => {
    switch (action.type) {
    case ADD_SUPPORT_MESSAGE: {
        let newData = {
            text: state.newTextField,
            user: '1'
        }
        return {
            ...state,
            messages: [...state.messages, newData],
            newTextField: ''
        }
    }
    case UPDATE_NEW_SUPPORT_TEXTFIELD: {
        return {
            ...state,
            newTextField: action.newText
        }
    }
    default:
        return state;
}
}

export const addSupportMessageActionCreator = () => {
    return {
        type: ADD_SUPPORT_MESSAGE
    }
}

export const updateNewSupportMessageFieldActionCreator = (text) => {
    return {
        type: UPDATE_NEW_SUPPORT_TEXTFIELD,
        newText: text
    }
}

export default supportPageReducer;