import React from "react"
import { connect } from "react-redux";
import { Navigate, useLocation, useNavigate, useParams } from "react-router-dom";
import { AuthAPI } from "../API/Api";
import Preloader from "../Components/Users/preloader";
import { authUser } from './../Redux/authReducer'

export const withAuthRedirect = (Component) => {
    class RedirectComponent extends React.Component {
        componentDidMount() {
            AuthAPI.authMe().then(response => {
                if (response.data.resultCode === 0) {
                    this.props.authUser(
                        response.data.data.id,
                        response.data.data.email,
                        response.data.data.login,
                        true
                    );
                } else {
                    this.props.authUser(
                        null,
                        null,
                        null,
                        false
                    );
                }
            })
        }
        render() {
            if (this.props.isAuthorized === false) {
                console.log("redirected")
                return <Navigate to={'/signin'} />
            } else {
                console.log('Component')
                return <Component {...this.props} />
            }
        }
    }
    let mapAuthStateToProps = (state) => {
        return {
            isAuthorized: state.authPage.data.isAuthorized
        }
    }
    let AuthRedirectComponent = connect(mapAuthStateToProps, { authUser })(RedirectComponent);
    return AuthRedirectComponent;
}

export const withRouter = (Component) => {
    function ComponentWithRouterProp(props) {
        let location = useLocation();
        let navigate = useNavigate();
        let params = useParams();
        return (
            <Component
                {...props}
                router={{ location, navigate, params }}
            />
        );
    }

    return ComponentWithRouterProp;
}



