import React from "react";
import p from './support.module.css';


let textField = React.createRef();

let Support = (props) => {
    return <div>
        <MakeMessages item={props.state.messages} />
        <textarea
            ref={textField}
            value={props.state.newTextField}
            onChange={() => {
                let text = textField.current.value;
                props.updateNewSupportMessageField(text);
            }}
        ></textarea>
        <button onClick={() => {props.addSupportMessage()}}>Send</button>
    </div>
}

const MakeMessages = (props) => {
    let messages2 = props.item.map(p => <MakeMessage user={p.user} text={p.text} />);
    return messages2;
}

const MakeMessage = (props) => {
    if (props.user === '1') {
        return <div className={p.myMessage}>
            <p>{props.text}</p>
        </div>
    } else if (props.user === '2') {
        return <div className={p.alienMessage}>
            <p>{props.text}</p>
        </div>
    }
}

export default Support;