import {ProfileAPI} from "../API/Api.js";

const ADD_POST = 'profilePageReducer-ADD-POST';
const UPDATE_NEW_POST_FIELD = 'profilePageReducer-UPDATE-NEW-POST-FIELD';
const SET_PROFILE = 'profilePageReducer-SET-PROFILE';
const UPDATE_LOADING = 'profilePageReducer-UPDATE-LOADING';
const UPDATE_STATUS_FIELD = 'profilePageReducer-UPDATE_STATUS_FIELD';
const SET_STATUS = 'profilePageReducer-SET-STATUS';

let initialState = {
    newPostField: 'What\'s new?',
    mainInfo: {
        aboutMe: '',
        fullName: '',
        photos: {
            small: '',
            large: ''
        },
        userId: ''
    },
    commentsData: [
        { text: 'Every morning that i wake up it turns out that u are doge', person: 'Banana boy', src: 'https://ecomarket.ru/imgs/products/9196/banan---1-sht--1.1636984147.jpg' },
        { text: 'its ok to be a doge, my friend', person: 'Mr galaxy', src: 'https://samsungstore.ru/upload/files/product/113253_1.jpg' },
        { text: 'Im Itachi Uchiha', person: 'Itachi Uchiha', src: 'https://kartinkin.net/uploads/posts/2021-07/1625690189_58-kartinkin-com-p-itachi-uchikha-art-krasivii-art-krasivo-65.jpg' }
    ],
    friends: [
        { person: 'Itachi Uchiha', src: 'https://kartinkin.net/uploads/posts/2021-07/1625690189_58-kartinkin-com-p-itachi-uchikha-art-krasivii-art-krasivo-65.jpg' },
        { person: 'Banana boy', src: 'https://ecomarket.ru/imgs/products/9196/banan---1-sht--1.1636984147.jpg' },
        { person: 'Itachi Uchiha', src: 'https://kartinkin.net/uploads/posts/2021-07/1625690189_58-kartinkin-com-p-itachi-uchikha-art-krasivii-art-krasivo-65.jpg' },
        { person: 'Banana boy', src: 'https://ecomarket.ru/imgs/products/9196/banan---1-sht--1.1636984147.jpg' }
    ],
    isLoading: false,
    status: ''
}

const profilePageReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST: {
            let newData = {
                text: action.text,
                person: state.mainInfo.fullName,
                src: state.mainInfo.photos.large
            }
            return {
                ...state,
                commentsData: [...state.commentsData, newData]
            }
        }
        case UPDATE_NEW_POST_FIELD: {
            return {
                ...state,
                newPostField: action.newText
            }
        }
        case SET_PROFILE: {
            return {
                ...state,
                mainInfo: { ...action.data }
            }
        }
        case UPDATE_LOADING: {
            return {
                ...state,
                isLoading: action.loading
            }
        }
        case UPDATE_STATUS_FIELD: {
            return {
                ...state,
                status: action.text
            }
        }
        case SET_STATUS: {
            return {
                ...state,
                status: action.status
            }
        }
        default: {
            return state;
        }
    }
}
export const PutStatusThunk = (text) => {
    return (dispatch) => {
        ProfileAPI.putStatus(text).then(response => {
            if (response.data.resultCode === 0) {
                dispatch(setStatus(text))
            }
        })
    }
}


export const GetStatusThunkAC = (id) => {
    return (dispatch) => {
        ProfileAPI.getStatus(id).then(response => {
            dispatch(setStatus(response.data))
        })
    }
}

export const ProfilePageThunkAC = (router) => {
    return (dispatch) => {
        let id = router.params.userId;
        dispatch(updateLoading(true))
        ProfileAPI.get(id).then(response => {
            console.log(response)
            dispatch(setProfile(response.data))
            dispatch(updateLoading(false))
        })
    }
}

export const setStatus = (status) => {
    return {
        type: SET_STATUS,
        status: status
    }
}

export const updateStatusField = (text) => {
    return {
        type: UPDATE_STATUS_FIELD,
        text: text
    }
}

const updateLoading = (loading) => {
    return {
        type: UPDATE_LOADING,
        loading: loading
    }
}

export const addPost = (text) => {
    return {
        type: ADD_POST,
        text: text
    }
}

const setProfile = (data) => {
    return {
        type: SET_PROFILE,
        data: data
    }
}

export default profilePageReducer;