const ADD_MESSAGE = 'ADD-MESSAGE';

let initialState = {
        dialogsData: [
            { id: 1, message: 'Hey. how r u doing?', person: 'Jake' },
            { id: 2, message: 'U owe me some money', person: 'Anton' },
            { id: 3, message: 'My sister is pregnant', person: 'Grisha' },
            { id: 4, message: 'Happy birthday', person: 'Alex' },
            { id: 5, message: 'I ate ur cake', person: 'Gosha' }
        ],
        currentDialogID: 1,
        messagesData: [
            {person: 1, text: 'When will u get me the money huh?'},
            {person: 2, text: 'Hi, um mb never...'},
            {person: 1, text: 'Whaaaaat?'}]            
}

const dialogsPageReducer = (state = initialState, action) => {
    switch (action.type) {
    case ADD_MESSAGE: {
        let newData = {
            text: action.text,
            person: 1
        }
        return {
            ...state,
            messagesData: [...state.messagesData, newData]
        }
    }
    default: {
        return state;
    }
    
}
}

export const addMessage = (text) => {
    return {
        type: ADD_MESSAGE,
        text: text
    }
}

export default dialogsPageReducer;