import React, { Suspense } from 'react';
import s from './App.module.css';
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Products from './Components/Products/Products';
import SupportContainer from './Components/Support/SupportContainer';
import { SignInContainer } from './Components/Sign in/signin';
import UsersContainer from './Components/Users/usersContainer';
import HeaderContainer from './Components/Header/HeaderContainer';
import { connect, Provider } from 'react-redux';
import { initializeThunk } from './Redux/authReducer';
import Preloader from './Components/Users/preloader';
import { compose } from 'redux';
import { withRouter } from './hoc/hoc';
import Example from './Example/Example';
import store from './Redux/redux-store.js';

const DialogsContainer = React.lazy(() => import('./Components/Dialogs/DialogsContainer'));
const ProfileContainer = React.lazy(() => import('./Components/Profile/ProfileContainer'));

class App extends React.Component {
    componentDidMount() {
        this.props.initializeThunk()
    }

    RenderTree() {
        return (
            <div className={s.page}>
                <div className={s.header}><HeaderContainer /></div>
                <div></div> {/*     */}
                <div className={s.main}>
                    <div></div> {/*     */}
                    <div>
                        <Routes>
                            <Route path='/dialogs' element={
                                <Suspense fallback={<div>Loading...</div>}>
                                    <DialogsContainer />
                                </Suspense>
                            } />
                            <Route path='/profile/:userId' element={
                                <Suspense fallback={<div>Loading...</div>}>
                                    <ProfileContainer />
                                </Suspense>
                            } />
                            <Route path='/profile' element={
                                <Suspense fallback={<div>Loading...</div>}>
                                    <ProfileContainer />
                                </Suspense>
                            } />
                            <Route path=':user?' element={<Products />} />
                            <Route path='/support' element={<SupportContainer />} />
                            <Route path='/signin' element={<SignInContainer />} />
                            <Route path='/users' element={<UsersContainer />} />
                            <Route path='/example' element={<Example />} />
                        </Routes>
                    </div>
                    <div></div> {/*     */}
                </div>D
                <div></div> {/*     */}
            </div>
        )
    }
    render() {
        console.log('rendered tree')
        return ((this.props.loading) ? <Preloader /> : <this.RenderTree />)
    }
}



const mapStateToProps = (state) => {
    return {
        loading: state.authPage.loading
    }
}

const AppContainer = compose(
    withRouter,
    connect(mapStateToProps, { initializeThunk })
)(App);

const AppStore = () => {
    return <div>
        <Provider store={store}>
        <BrowserRouter>
            
                <AppContainer />
            
        </BrowserRouter>
        </Provider>
    </div>
}



export default AppStore;
