import React from 'react';
import h from './Header.module.css';
import { NavLink } from 'react-router-dom';
import logo from '../../Redux/Pictures/logo.png';

const Header = (props) => {
  const Logout = (props) => {
    return <p
      className={h.item}
      onClick={props.logoutThunk}>Logout
    </p>
  }
  return <header className={h.header}>
    <div className={h.header1}>
      <img src={logo}></img>
      <NavLink to='/friends/' className={navData => navData.isActive ? h.active : h.item}>FRIENDS</NavLink>
      <NavLink to='/dialogs/' className={navData => navData.isActive ? h.active : h.item}>MESSAGES</NavLink>
      <NavLink to='/profile/26279' className={navData => navData.isActive ? h.active : h.item}>PROFILE</NavLink>
      <NavLink to='/products/' className={navData => navData.isActive ? h.active : h.item}>PRODUCTS</NavLink>
      <NavLink to='/support/' className={navData => navData.isActive ? h.active : h.item}>SUPPORT</NavLink>
      <NavLink to='/users/' className={navData => navData.isActive ? h.active : h.item}>FIND</NavLink>
      <NavLink to='/example/' className={navData => navData.isActive ? h.active : h.item}>Example</NavLink>
    </div>
    <div className={h.header2}>
      <div>
        {(props.isAuthorized === true) ?
          <Logout logoutThunk={props.logoutThunk} />
          : <NavLink to='/signin/' className={navData => navData.isActive ? h.active : h.item}>SIGN IN</NavLink>
        }
      </div>
      <button className={h.offer}>{(props.isAuthorized === true) ? 'Authorized' : 'Not authorized'}</button>
    </div>
  </header>
}



export default Header;