import { stopSubmit } from "redux-form";
import { AuthAPI } from "../API/Api";

let AUTH_USER = 'AUTH-USER';
let CHANGE_LOADING = 'CHANGE-LOADING';

let initialState = {
    data: {
        id: null,
        email: null,
        login: null,
        isAuthorized: false,
        loggingResultCode: undefined,
    },
    loading: false
}

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case AUTH_USER: {
            return {
                ...state,
                data: { ...action.data }
            }
        }
        case CHANGE_LOADING: {
            return {
                ...state,
                loading: action.loading
            }
        }
        default: {
            return state;
        }
    }
}

export default authReducer;

const changeLoading = (loading) => {
    return {
        type: CHANGE_LOADING,
        loading: loading
    }
}

export const initializeThunk = () => {
    return (dispatch) => {
        dispatch(changeLoading(true))
        let promise = dispatch(authMeThunk())
        promise.then(() => {
            dispatch(changeLoading(false))
        }
        )
    }
}

export const authMeThunk = () => {
    return (dispatch) => {
        return AuthAPI.authMe().then(response => {
            if (response.data.resultCode === 0) {
                dispatch(authUser(
                    response.data.data.id,
                    response.data.data.email,
                    response.data.data.login,
                    true,
                    response.data.resultCode
                ));
            } else {
                dispatch(authUser(
                    null,
                    null,
                    null,
                    false,
                    response.data.resultCode
                ));

            }
        })
    }
}

export const authUser = (id, email, login, isAuthorized, resultCode) => {
    return {
        type: AUTH_USER,
        data: {
            id: id,
            email: email,
            login: login,
            isAuthorized: isAuthorized,
            loggingResultCode: resultCode
        },
    }
}

export const loginThunk = (email, password, rememberMe = false) => {
    return (dispatch) => {
        AuthAPI.authLoginPost(email, password, rememberMe).then(response => {
            if (response.data.resultCode === 0) {
                AuthAPI.authMe().then(response => {
                    if (response.data.resultCode === 0) {
                        dispatch(authUser(
                            response.data.data.id,
                            response.data.data.email,
                            response.data.data.login,
                            true,
                            response.data.resultCode
                        ));
                    } else {
                        dispatch(authUser(
                            null,
                            null,
                            null,
                            false,
                            response.data.resultCode
                        ));
                    }
                })
            } else {

                dispatch(stopSubmit('Login', { _error: response.data.messages }));
                dispatch(authUser(
                    null,
                    null,
                    null,
                    false,
                    response.data.resultCode
                ));
            }
        })
    }
}

export const logoutThunk = () => {
    return (dispatch) => {
        AuthAPI.authLoginDelete().then(response => {
            if (response.data.resultCode === 0) {
                AuthAPI.authMe().then(response => {
                    if (response.data.resultCode === 0) {
                        dispatch(authUser(
                            response.data.data.id,
                            response.data.data.email,
                            response.data.data.login,
                            true,
                            0
                        ));
                    } else {
                        dispatch(authUser(
                            null,
                            null,
                            null,
                            false,
                            0
                        ));
                    }
                })
            }
        })
    }
}